.blog-list {
    display: grid;
    grid-template-columns: 1fr 1fr ;
    grid-gap: 40px;
    max-width: 1300px;
    margin: 40px auto;
  }
    .blog-list .card {
        background: #fff;
        color: #333;
        padding: 20px;
        border-radius: 4px;
        box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
        transition: all 0.3s ease;
        position: relative;
    }
    .blog-list .card:hover {
        transform: scale(1.1);
    }
    .blog-list .card h3 {
        color: #786fa6;
        margin-bottom: 6px;
    }
    .blog-list .card p {
        color: #778beb;
        font-size: 0.9em;
    }
    .blog-list .card div {
        color: #555;
        font-size: 0.7em;
        margin: 20px 0;
        line-height: 1.5em;
    }
    .blog-list .card a {
        color: #786fa6;
        text-decoration: none;
        display: block;
        background: #e2e2e2;
        font-size: 0.9em;
        width: 150px;
        text-align: center;
        border-radius: 4px;
        margin: 20px auto 0;
    }

    .blog-list .card.dark {
        background: #555;
      }
      .blog-list .card.dark p,
      .blog-list .card.dark h3,
      .blog-list .card.dark div {
        color: #e4e4e4;
      }
      
      .delete{
        position: absolute;
        top: 10px;
        right: 10px;
        cursor: pointer;
        filter: invert(20%);
      }
      .update{
        position: absolute;
        top: 10px;
        right: 50px;
        cursor: pointer;
        filter: invert(20%);
      }
      .delete.dark{
          filter: invert(100%);
      }
      .update.dark{
        filter: invert(100%);
    }
