@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:ital,wght@0,100;0,200;0,300;0,900;1,100;1,200;1,300;1,400;1,500;1,900&display=swap');

html, body, #root{
  height: 100%;
}

body {
  margin: 0;
  font-family: 'Fira Sans', sans-serif;
  font-size: 1.4em;
  background: #dfdfdf;
  color: #333;
}

h1,h2,h3,p{
  margin: 0;
}
input, textarea{
  color: #333;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}

.page-title{
  text-align: center;
  margin: 40px auto;
  color: #786fa6;
}

.loading, .error{
  text-align: center;
  margin: 40px auto;
  color: #c44569;
}